/* 기본이 들어가기 */
@media screen and (min-width:1320px){
  header>div,article>div{
    width:1320px;
  }
  #box01>p{
    width:1320px;
  }
  #box02>div{
    width: 980px;
  }
  #box03>div{
    width:1103px;
  
  }
  #box06>div{
    width: 1103px;
  }

}

@media screen and (max-width:1319px) and (min-width:1103px){
  #box02>div{
    width: 980px;
  }
  #box03>div{
    width:1103px;
  
  }
  #box06>div{
    width: 1103px;
  }
}

@media screen and (max-width:1102px) and (min-width:980px){
  #box02>div{
    width: 980px;
  }
}
@media screen and (max-width:979px) and (min-width:580px){
  #wrap>header{
    padding:25px 0;
    }
    header>div{
    display:block;
    text-align:center;
    }
    header>div>h1>a{
      font-size:36px;
    }
    header>#btn{
      display:flex;
      top:25px;
    }
  nav{
    position:fixed;
    /* left:0;  */
    left:-480px;
    top:0;
    width:400px;
    height:100%;
    padding:36px;
    box-sizing:border-box;
    background-color:rgba(0,0,0,0.8);
    z-index:999;
  }
  nav>ul{
    width:100%;
    height:100%;
    display:flex;
    flex-direction: column;
    justify-content: center;
    /* background-color:red; */
  }
  nav>ul>li{
    display:block;
    margin-left:0;
    border-bottom:1px solid #fff;
  }
  nav>ul>li:first-child{
    border-top:1px solid #fff;
  }
  nav>ul>li>a{
    line-height:60px;
  }
 nav>.close{
   position:absolute;
   width:25px; height:25px;
   right:-50px; top:25px;
   background-color:red;
 }
 #box01>h3{
  position: absolute;
  left:0; top:50%;
  /* 문제가 없어서 통과 */
  width:100%; height:auto;
  border:none;
  margin: -90px 0 0 0px;
  line-height:1.5;
  display:block;
  text-align:center;
  font-size:60px;
 }
 #box01>ul>li{
  background-size:auto 100%;
 }
#box01>p{
  display:none;
}
/* box02 */
#box02>div{
  width:98%/* 980px */;
  justify-content: space-evenly;
}
/* box03 */
#box03 .box{
  /* width:auto; */
  padding-left:0;
  position:static;
}
#box03 .box>figure{
  position:static;
  width:380px;
  margin: 0 auto 35px;

}
/* #box03 .box>figure>img{
  max-width:100%;
} */
#box03 .box>div{
  width:90.51020408163265%;
  margin: 0 auto;
  text-align:center;
}
#box03 .box>div>.text{ 
 text-align:justify;
}

#box04 .box{
  display:block;
}
#box04 .box>div{
  width:90.51020408163265%;
  text-align:center;
  margin-bottom:50px;
}
#box04 .box>ul{
 width: 576px;
 margin: 0 auto;
}

/* box05 */

/* gallery */
#box05 .gallery{
  width:96vw;
  margin: 0 auto;
}
#show{
  width:78vw;
  margin: 0 auto;
  overflow:hidden;}
#all{
  /* display:flex;
  flex-wrap:wrap; */
  width:156vw;
  justify-content:flex-start;}
#all>figure{
  /*  미디어 갤러리 정리 ->vw */
  width:24vw;
  margin:0 1vw 17px;
}
/*  버튼 */
#box05 .gallery>p{
  position:absolute;
  width:1.5vw;
  height:1.5vw;
  top:40%;
  margin-top:-0.75vw;
  background-color:red;
  display:block;
  text-indent:-9999px;
}
#box05 .gallery>p.prev{
  left:2vw;
}
#box05 .gallery>p.next{
  right:2vw;
}
/* box06 */
#box06>div{
  width:100%/* 1103px */;
}
#box06 .box{
  display:block;
}
#box06 .box>div{
  width:100%;
  margin: 0 auto 45px;
  text-align:center;

}
#box06 .box>div>.text{
  width:90.51020408163265%;
  margin-bottom:0px;
  margin: 0 auto;
}
#box06 .box>div>dl{
  margin-bottom:0;
  text-align:left;
  width:77.85714285714286%;
  margin: 0 auto;
}
#box06 .box>div>dl>dt{
 display:inline;
  background-size:18px;
}
#box06 .box>div>dl>dd{
  padding-left:0px;
  display:inline;
}

#box06 .box>form{
  width:77.85714285714286%/* 445px */;
  margin-right:0;
  margin: 0 auto;

}
/* aside */
aside .sument{
  display:none;
}
aside .top{
  display:none;
}
